body{
  font-family: 'Roboto', sans-serif;
  background-color: var(--app--bg--color);
  transition: color 300ms, background-color 300ms;

  --app--bg--color: #fff;
  --app--color: #242424;

  --nav--bg--color: #fff;

  --btn--primary--main--color: #43a047;
  --btn--primary--side--color: #2e7d32;
  --btn--secondary--main--color: #1e88e5;
  --btn--secondary--side--color: #1565c0;
  --btn--tertiary--main--color: #e53935;
  --btn--tertiary--side--color: #c62828;

  --inp--primary--inp--color: #242424;
  --inp--primary--ph--color: #bdbdbd;
  --inp--primary--lbl--color: #bdbdbd;
  --inp--primary--lbl--focus--color: #43a047;
  --inp--primary--und--color: #43a047;
  --inp--secondary--inp--color: #242424;
  --inp--secondary--ph--color: #bdbdbd;
  --inp--secondary--lbl--color: #bdbdbd;
  --inp--secondary--lbl--focus--color: #1e88e5;
  --inp--secondary--und--color: #1e88e5;

  --dialog--bg--color: #fff;
}
body *{
  transition: background-color 300ms;
}
body[darkmode]{
  --app--bg--color: #242424;
  --app--color: #fff;

  --nav--bg--color: #363636;

  --btn--primary--main--color: #43a047;
  --btn--primary--side--color: #2e7d32;
  --btn--secondary--main--color: #1e88e5;
  --btn--secondary--side--color: #1565c0;
  --btn--tertiary--main--color: #e53935;
  --btn--tertiary--side--color: #c62828;

  --inp--primary--inp--color: #fff;
  --inp--primary--ph--color: #bdbdbd;
  --inp--primary--lbl--color: #bdbdbd;
  --inp--primary--lbl--focus--color: #43a047;
  --inp--primary--und--color: #43a047;
  --inp--secondary--inp--color: #fff;
  --inp--secondary--ph--color: #bdbdbd;
  --inp--secondary--lbl--color: #bdbdbd;
  --inp--secondary--lbl--focus--color: #1e88e5;
  --inp--secondary--und--color: #1e88e5;

  --dialog--bg--color: #363636;
}


.navbar{
  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  background-color: var(--nav--bg--color);
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.20);
  padding: 10px;
}
.navbar-left{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.navbar-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-right{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}


.button-small{
  padding: 6px 20px;
}

.button-medium{
  padding: 10px 24px;
}

.button-large{
  padding: 14px 28px;
}

.button{
  position: relative;
  margin: 5px 5px 10px 5px;
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  border: 0px transparent;
  border-radius: 5px;
  transition: 150ms;
  transform: translateY(0);
  cursor: pointer;
  box-shadow: 0px 5px 5px 5px rgba(0, 0, 0, 0.20);
}
.button::before{
  content: '';
  position: absolute;
  top: 0%;
  left: 0%;
  right: 0%;
  bottom: 0%;
  background-color: #bdbdbd;
  border-radius: 5px;
  z-index: -1;
}
.button::after{
  content: '';
  position: absolute;
  top: 5px;
  left: 0%;
  right: 0%;
  bottom: -5px;
  background-color: #9e9e9e;
  border-radius: 5px;
  z-index: -2;
  transition: 150ms;
  transform: translateY(0);
}
.button:focus{
  outline: none;
}
.button:hover{
  transform: translateY(2px);
}
.button:hover::after{
  transform: translateY(-2px);
}
.button:active{
  transform: translateY(4px);
}
.button:active::after{
  transform: translateY(-4px);
}

.button-primary::before{
  background-color: var(--btn--primary--main--color) !important;
}
.button-primary::after{
  background-color: var(--btn--primary--side--color) !important;
}
.button-secondary::before{
  background-color: var(--btn--secondary--main--color) !important;
}
.button-secondary::after{
  background-color: var(--btn--secondary--side--color) !important;
}
.button-tertiary::before{
  background-color: var(--btn--tertiary--main--color) !important;
}
.button-tertiary::after{
  background-color: var(--btn--tertiary--side--color) !important;
}


.input-container{
  position: relative;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}
.input-container[type="text"] .input-icon, .input-container[type="email"] .input-icon, .input-container[type="password"] .input-icon{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.input-container[type="text"] .input-icon .material-icons, .input-container[type="email"] .input-icon .material-icons, .input-container[type="password"] .input-icon .material-icons{
  color: var(--app--color);
  font-size: 26px;
  cursor: pointer;
}

.input-container-inner[type="text"], .input-container-inner[type="email"], .input-container-inner[type="password"]{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 5px;
  width: 100%;
}
.input-container-inner[type="text"]::after, .input-container-inner[type="email"]::after, .input-container-inner[type="password"]::after{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #bdbdbd;
  transform: scaleY(1);
  transform-origin: bottom;
  transition: 150ms;
}
.input-container-inner[type="text"]:focus-within:after, .input-container-inner[type="email"]:focus-within:after, .input-container-inner[type="password"]:focus-within:after{
  transform: scaleY(2);
}
.input-container-inner[type="text"]::before, .input-container-inner[type="email"]::before, .input-container-inner[type="password"]::before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #bdbdbd;
  transform: scaleX(0);
  transform-origin: center;
  z-index: 10;
  transition: 150ms;
}
.input-container-inner[type="text"]:focus-within::before, .input-container-inner[type="email"]:focus-within::before, .input-container-inner[type="password"]:focus-within::before{
  transform: scaleX(1);
}

.input-container-inner[type="text"] input[type="text"], .input-container-inner[type="email"] input[type="email"], .input-container-inner[type="password"] input[type="password"]{
  background-color: transparent;
  border: 0px transparent;
  padding: 10px 0px;
  font-size: 16px;
  font-weight: 400;
  width: 100%;
}
.input-container-inner[type="text"] input[type="text"]:focus, .input-container-inner[type="email"] input[type="email"]:focus, .input-container-inner[type="password"] input[type="password"]:focus{
  outline: none;
}
.input-container-inner[type="text"] input[type="text"]::placeholder, .input-container-inner[type="email"] input[type="email"]::placeholder, .input-container-inner[type="password"] input[type="password"]::placeholder{
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
  color: #757575;
  opacity: 1;
  transition: 150ms;
}
.input-container-inner[type="text"][label] input[type="text"]::placeholder, .input-container-inner[type="email"][label] input[type="email"]::placeholder, .input-container-inner[type="password"][label] input[type="password"]::placeholder{
  opacity: 0;
}
.input-container-inner[type="text"]:focus-within input[type="text"]::placeholder, .input-container-inner[type="email"]:focus-within input[type="email"]::placeholder, .input-container-inner[type="password"]:focus-within input[type="password"]::placeholder{
  opacity: 1;
}

.input-container-inner[type="text"] label, .input-container-inner[type="email"] label, .input-container-inner[type="password"] label{
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
  user-select: none;
  pointer-events: none;
  color: #757575;
  transition: 150ms;
}
.input-container-inner[type="email"] input[type="email"]:invalid + label{
  color: #e53935;
}
.input-container-inner[type="text"]:focus-within label, .input-container-inner[type="text"] input[type="text"]:not(:placeholder-shown) + label, .input-container-inner[type="email"]:focus-within label, .input-container-inner[type="email"] input[type="email"]:not(:placeholder-shown) + label, .input-container-inner[type="password"]:focus-within label, .input-container-inner[type="password"] input[type="password"]:not(:placeholder-shown) + label{
  transform: translateY(-150%);
}

.input-container-inner-primary input[type="text"], .input-container-inner-primary input[type="email"], .input-container-inner-primary input[type="password"]{
  color: var(--inp--primary--inp--color);
}
.input-container-inner-primary::before{
  background-color: var(--inp--primary--und--color) !important;
}
.input-container-inner-primary[type="text"] input[type="text"]::placeholder, .input-container-inner-primary[type="email"] input[type="email"]::placeholder, .input-container-inner-primary[type="password"] input[type="password"]::placeholder{
  color: var(--inp--primary--ph--color);
}
.input-container-inner-primary[type="text"] label, .input-container-inner-primary[type="email"] label, .input-container-inner-primary[type="password"] label{
  color: var(--inp--primary--lbl--color);
}
.input-container-inner-primary[type="text"]:focus-within label, .input-container-inner-primary[type="email"]:focus-within label, .input-container-inner-primary[type="password"]:focus-within label{
  color: var(--inp--primary--lbl--focus--color);
}
.input-container-inner-secondary input[type="text"], .input-container-inner-secondary input[type="email"], .input-container-inner-secondary input[type="password"]{
  color: var(--inp--secondary--inp--color);
}
.input-container-inner-secondary::before{
  background-color: var(--inp--secondary--und--color) !important;
}
.input-container-inner-secondary[type="text"] input[type="text"]::placeholder, .input-container-inner-secondary[type="email"] input[type="email"]::placeholder, .input-container-inner-secondary[type="password"] input[type="password"]::placeholder{
  color: var(--inp--secondary--ph--color);
}
.input-container-inner-secondary[type="text"] label, .input-container-inner-secondary[type="email"] label, .input-container-inner-secondary[type="password"] label{
  color: var(--inp--secondary--lbl--color);
}
.input-container-inner-secondary[type="text"]:focus-within label, .input-container-inner-secondary[type="email"]:focus-within label, .input-container-inner-secondary[type="password"]:focus-within label{
  color: var(--inp--secondary--lbl--focus--color);
}

.link{
  position: relative;
  display: inline-block;
  margin: 0 5px;
}
.link a{
  display: inline-block;
  font-weight: 500;
  font-size: 14px;
  text-transform: capitalize;
  text-decoration: none;
  color: #1e88e5;
  padding: 5px;
  cursor: pointer;
  transition: 150ms;
}
.link a:focus{
  outline: none;
  background-color: rgba(30, 136, 229, 0.2);
}
.link .tooltip{
  position: absolute;
  padding: 5px;
  bottom: 150%;
  left: 50%;
  opacity: 0;
  font-size: 12px;
  color: var(--app--color);
  background-color: rgba(255, 213, 79, 0.2);
  border: 2px solid #ffc107;
  transform: translateX(-50%);
  z-index: 1000;
  pointer-events: none;
  transition: 150ms;
  transition-delay: 300ms;
}
.link:hover a{
  background-color: rgba(30, 136, 229, 0.2);
}
.link:hover .tooltip{
  opacity: 1;
}

.dialog-container{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  z-index: 1000;
  pointer-events: none;
}
.dialog{
  pointer-events: all;
  cursor: auto;
  z-index: 1500;
}
.dialog-inner{
  position: relative;
  width: 100%;
  height: 100%;
}



.login-popup{
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 10px;
  width: 210px;
  background-color: var(--dialog--bg--color);
  padding: 10px;
  color: var(--app--color);
  border-radius: 5px;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.20);
}
.login-popup-enter > .login-popup{
  bottom: -300px;
}
.login-popup-enter-done > .login-popup{
  bottom: 0;
  transition: 300ms;
  transition-delay: 700ms;
}
.login-popup-exit > .login-popup{
  bottom: -300px;
  transition: 300ms;
}
.login-popup-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  align-items: center;
}
.login-popup-inner-info{
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-popup-inner-info svg{
  
}
.login-popup-inner-info > p{
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  width: 100%;
  text-align: center;
}
.login-popup-inner > .container{
  display: flex;
  justify-content: center;
  align-items: center;
}


.user-form{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%) scale(1);
  background-color: var(--dialog--bg--color);
  color: var(--app--color);
  border-radius: 5px;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.20);
}
.user-form-enter > .user-form{
  transform: translate(-50%,-50%) scale(0.75);
  opacity: 0;
}
.user-form-enter-done> .user-form{
  transform: translate(-50%,-50%) scale(1);
  opacity: 1;
  transition: 300ms;
}
.user-form-exit > .user-form{
  transform: translate(-50%,-50%) scale(0.75);
  opacity: 0;
  transition: 300ms;
}

.user-form-inner{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.user-form-logo-container{
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.user-form-logo{
  position: relative;
  width: 75px;
  height: 75px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: var(--dialog--bg--color);
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.20);
}
.user-form-logo::before{
  content: '';
  position: absolute;
  width: 150%;
  height: 70%;
  top: 51%;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--dialog--bg--color);
  transition: background-color 300ms;
}
.user-form-logo-inner{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.user-form-logo1-inner-enter{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-25%);
  opacity: 0;
}
.user-form-logo1-inner-enter-done{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 1;
  transition: 300ms;
}
.user-form-logo1-inner-exit{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-25%);
  opacity: 0;
  transition: 300ms;
}
.user-form-logo2-inner-enter{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-75%);
  opacity: 0;
}
.user-form-logo2-inner-enter-done{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 1;
  transition: 300ms;
}
.user-form-logo2-inner-exit{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-75%);
  opacity: 0;
  transition: 300ms;
}

.user-form > .container{
  margin: 10px;
}
.user-form-login{
  margin: 50px 10px 10px 10px;
}
.user-form-login form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5px;
  width: 260px;
}
.user-form-login form .input-container{
  width: 100%;
}
.user-form-info-feedback{
  position: relative;
  text-align: center;
  border-top: 2px solid #e53935;
  border-bottom: 2px solid #e53935;
  background-color: rgba(229, 57, 53, 0.15);
  padding: 10px;
  margin: 5px;
  width: 100%;
}
.user-form-info-feedback-enter{
  transform: scaleY(0.75);
  transform-origin: center center;
  opacity: 0;
}
.user-form-info-feedback-enter-done{
  transform: scaleY(1);
  transform-origin: center center;
  opacity: 1;
  transition: 300ms;
}
.user-form-info-feedback-exit{
  transform: scaleY(0.75);
  transform-origin: center center;
  opacity: 0;
  transition: 300ms;
}
.user-form-button-group{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.user-form-register-shortcut{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
}
.user-form-register-shortcut p{
  width: 100px;
  text-align: center;
  margin: 0;
  color: var(--app--color);
  font-weight: 500;
  font-size: 12px;
}
.user-form-signup-input-enter{
  transform: translateY(-50%);
  opacity: 0;
}
.user-form-signup-input-enter-done{
  transform: translateY(0);
  opacity: 1;
  transition: 300ms;
}
.user-form-signup-input-exit{
  transform: translateY(-50%);
  opacity: 0;
  transition: 300ms;
}

.ui-messenger{
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 10px;
  background-color: var(--dialog--bg--color);
  color: var(--app--color);
  border-radius: 5px;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.20);
  max-width: 500px;
  z-index: 2000;
  transition: 300ms;
}
.ui-messenger p{
  margin: 5px;
}
.ui-messenger-enter > .ui-messenger{
  top: -300px;
}
.ui-messenger-enter-done > .ui-messenger{
  top: 50px;
  transition: 300ms;
}
.ui-messenger-exit > .ui-messenger{
  top: -300px;
  transition: 300ms;
}